.gallery {
  background-color: @white;
  width: 100%;
  min-width: @mobile-width;
  margin-bottom: 35px;

  @media (min-width: @tablet-width) {
    margin-bottom: 40px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 0;
  }
}
.gallery__photo-wrapper {
  list-style: none;

  @media (min-width: @tablet-width) {


    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
