.main-nav {
  min-width: @mobile-width;
  background-color: @dark-table;

  &--nojs .main-nav__toggle {
    display: none;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    background-color: transparent;
    align-items: center;
    margin: 0 auto;
    justify-content: space-between;
    max-width: 940px;
    padding-left: 10px;
    padding-right: 10px;
  }

  @media (max-width: 1199px) {
    &--closed .main-nav__item {
      display: none;
    }

    &--closed {
      background-color: transparent;
    }
  }

  &--closed .main-nav__span {
    display: block;
    width: 50px;
    height: 4px;
    background-color: @white;
    border-radius: 1px;

    &::before,
    &::after {
      content: "";
      position: absolute;
      left: 0;
      width: 50px;
      height: 4px;
      background-color: @white;
      border-radius: 1px;
    }

    &::before {
      top: 0;
    }

    &::after {
      bottom: 0;
    }
  }

  &--opened .main-nav__span {
    &::before,
    &::after {
      content: "";
      position: absolute;
      right: 0;
      top: 10px;
      width: 29px;
      height: 4px;
      background-color: @white;
      border-radius: 1px;
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }
  }
}

.main-nav__toggle {
  position: absolute;
  top: 21px;
  right: 20px;
  width: 50px;
  height: 24px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;

  @media (min-width: @tablet-width) {
    top: 43px;
    right: 0;
  }

  @media (min-width: @desktop-width) {
    display: none;
  }

  &:hover span,
  &:hover span::before,
  &:hover span::after,
  &:focus span,
  &:focus span::before,
  &:focus span::after {
    background-color: @pink;
  }

  &:active span,
  &:active span::before,
  &:active span::after {
    background-color: @dark-pink;
  }
}

.main-nav__logo-wrap {
  align-items: center;
  min-height: 66px;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  position: relative;
  max-width: 320px;
  box-sizing: border-box;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    min-height: 110px;
    max-width: 620px;
    padding: 0;
  }

  @media (min-width: @desktop-width) {
    width: 147px;
    padding: 0;
    margin: 0;
  }
}

.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
  background-color: @dark-table;

  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    flex-grow: 1;
    max-width: 650px;
    background-color: transparent;
  }
}

.main-nav__item {
  padding: 20px 0;
  margin: 0 auto;
  border-top: 1px solid @black;
  box-shadow: 0 1px 0 @black, inset 0 1px 0 @nav-shadow;
  text-align: center;
  height: 65px;
  box-sizing: border-box;
  width: auto;

  @media (min-width: @desktop-width) {
    box-shadow: none;
    border: none;
    padding: 0;
    height: auto;
    box-sizing: border-box;
    margin: 0;
    width: 135px;

    &:not(:nth-child(4n+4)) {
      margin-right: 36px;
    }

    &:nth-child(n+5) {
      margin-top: 10px;
    }
  }
}

.main-nav__link {
  color: @white;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  text-transform: uppercase;
  text-decoration: none;
  padding: 0;
  margin: 0;

  &:hover,
  &:focus {
    color: @pink;
    outline: none;
  }

  &:active {
    color: @dark-pink;
  }

  @media (min-width: @desktop-width) {
    line-height: 18px;
    font-weight: 400;
    text-transform: none;

    &--active {
      position: relative;

      &::after {
        content: "";
        position: absolute;
        right: 0;
        bottom: -10px;
        left: 0;
        height: 2px;
        background: @white;
      }
    }
  }
}
